
.page-wrapper {
  // outline: 1px dotted rgba(0, 0, 0, 0.3);
  display: flex;
  width: 320px;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  position: relative;

  margin: 0 auto;
}

.page-wrapper__footer {
  flex-direction: row;
}

.page-wrapper__card {
  align-content: flex-start;
}

.page-wrapper--paddings {
  width: 260px;
  margin: 0 auto;
}

.page-wrapper--smaller {
  width: 260px;
}

@media (min-width: $tablet-width) {
  .page-wrapper {
    width: $tablet-width;
  }

  .page-wrapper__row {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    flex-grow: 0;
    flex-shrink: 0;
    justify-content: space-between;
    align-items: center;
    margin-top: 32px;
  }

  .page-wrapper__header {
    display: flex;
    margin: 20px 50px;
    margin-top: 0;
  }

  .page-wrapper__card {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    justify-content: space-between;
  }

  .page-wrapper--paddings {
    width: $tablet-width - 50px * 2;
  }

  .page-wrapper--break {
    display: block;
    width: 250px;
  }
}

@media (min-width: $desktop-width) {
  .page-wrapper {
    width: $desktop-width;
  }

  .page-wrapper__header {
    width: $desktop-width;
    margin: 0;
    margin-bottom: 20px;
  }

  .page-wrapper--paddings {
    width: $desktop-width - 50px * 2;
  }

  .page-wrapper--break {
    width: 250px;
  }

  .page-wrapper--paddings-appear {
    width: $desktop-width - 50px * 2;
    margin: 0;
  }

  .page-wrapper__card {
    display: block;
  }

  .page-wrapper--decor {
    padding-bottom: 50px;
    position: relative;

    background-image: url("../img/review@1x.jpg");
    background-repeat: no-repeat;
    background-position: 0 0;
    background-size: contain;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/review@2x.jpg");
      background-size: 350px auto;
    }


    &::after {
      content: "";
      position: absolute;

      left: 0;
      bottom: -52px;
      width: 100%;
      height: 25px;

      background-image: url("../img/bg-zigzag-line.svg");
      background-repeat: no-repeat;
      background-position: 50% 50%;
    }
  }

  .page-wrapper--decor-no {
    background-image: none;
    padding-bottom: 0;
  }

  .page-wrapper--contacts-break {
    width: 230px;
    margin: 0;
    margin-top: -150px;
  }

  .page-wrapper--contacts {
    width: 300px;
  }
}
