.button {
  display: block;
  width: 100%;
  align-self: center;
  padding: 15px;
  text-decoration: none;
  font-size: 17px;
  font-weight: 700;
  text-transform: uppercase;
  line-height: 20px;
  text-align: center;
}

.button--light {
  color: $greyish-brown;
  background-color: white;
  border: solid 2px $greyish-brown;

  &:hover,
  &:focus {
    background-color: $greyish-brown;
    color: white;
  }

  &:active {
    color: rgba(255, 255, 255, 0.3);
  }
}

.button--slider {
  padding-top: 25px;
  padding-bottom: 25px;
  border: 1px solid $white;
  background-color: white;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  cursor: pointer;

  &:hover,
  &:focus {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }
}

.button--forward {
  background-image: url("../img/icon-left-arrow.svg");
}

.button--back {
  background-image: url("../img/icon-right-arrow.svg");
}


.button--green {
  border: none;
  background-color: $seafoam-blue;
  color: white;
  font-size: 17px;
  font-weight: 700;
  line-height: 16px;

  &:hover,
  &:focus {
    background-color: rgba(70, 193, 174, 1);
  }

  &:active {
    background-color: rgba(70, 193, 174, 1);
    color: rgba(255, 255, 255, 0.3);
  }
}

@media (min-width: $tablet-width) {
  .button--slider {
    border: none;
    padding-top: 20px;
  }
}
